import { h } from '../h.js'
import { rerender, resetHooks } from '../hooks.js'

let routes = []
let notFoundRoute = null
let initialized = false

export function Router({ children }) {
  const list = Array.isArray(children) ? children : [children]

  routes = []
  notFoundRoute = null

  list.forEach(v => {
    if (!v || v.type !== Route) return

    const route = Route(v.props)

    if (route.isNotFound) {
      notFoundRoute = route
    } else {
      routes.push(route)
    }
  })

  if (!initialized) {
    window.addEventListener('popstate', () => {
      resetHooks()
      rerender()
    })
    initialized = true
  }

  return resolveRoute()
}

export function Route({ path, component }) {
  // 404 / catch-all
  if (path === '*') {
    return {
      isNotFound: true,
      component
    }
  }

  const keys = []

  const regex = new RegExp(
    '^' +
      path.replace(/\/:([^/]+)/g, (_, key) => {
        keys.push(key)
        return '/([^/]+)'
      }) +
      '$'
  )

  return {
    path,
    component,
    regex,
    keys,
    isNotFound: false
  }
}

export function Link({ to, children, class: className }) {
  return h(
    'a',
    {
      href: to,
      class: className,
      onclick: e => {
        e.preventDefault()
        navigate(to)
      }
    },
    ...(Array.isArray(children) ? children : [children])
  )
}

/* ======================
   HISTORY
====================== */

export function navigate(path) {
  history.pushState({}, '', path)
  resetHooks()
  rerender()
}

export function replace(path) {
  history.replaceState({}, '', path)
  resetHooks()
  rerender()
}

export function back() {
  history.back()
}

export function forward() {
  history.forward()
}

/* ======================
   MATCHING
====================== */

function resolveRoute() {
  const current = location.pathname || '/'

  for (const route of routes) {
    const match = current.match(route.regex)
    if (!match) continue

    const params = {}
    route.keys.forEach((k, i) => {
      params[k] = match[i + 1]
    })

    return route.component({ params })
  }

  if (notFoundRoute) {
    return notFoundRoute.component()
  }

  return h(
    'div',
    { class: 'p-10 text-center text-red-400 text-xl' },
    '404 bro ðŸ˜µ'
  )
}
