let hooks = []
let index = 0
let rerenderFn = null

export function resetHooks() {
  index = 0
}

export function setRerender(fn) {
  rerenderFn = fn
}

export function rerender() {
  if (rerenderFn) rerenderFn()
}

export function useState(initial) {
  const i = index

  hooks[i] ??= initial

  const setState = value => {
    hooks[i] = typeof value === 'function'
      ? value(hooks[i])
      : value

    rerender()
  }

  index++
  return [hooks[i], setState]
}

export function useEffect(fn, deps) {
  const i = index
  const prev = hooks[i]

  const hasChanged =
    !prev ||
    !deps ||
    deps.some((d, j) => d !== prev[j])

  if (hasChanged) fn()

  hooks[i] = deps
  index++
}
